@font-face {
  font-family: Sofia;
  src: url(/static/media/sofiab.72c1413b.ttf);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  overflow-y: scroll;
  height: 100%;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
}

body {
  font-family: 'Space Grotesk', sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

input,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  font-size: 100%;
}

button {
  cursor: pointer;
}

button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.display-off {
  display: none !important;
}

.opaque {
  opacity: 0;
  pointer-events: none;
}

.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.disabledFull {
  -webkit-filter: blur(2.5px);
          filter: blur(2.5px);
  pointer-events: none;
  opacity: 0.4;
  /* cursor: not-allowed;  */

}

.pdt-major {
