@import '../../variables';
@import '../../mixins';

.notificationBox {
  position: fixed;
  z-index: 5;
  top: 1em;
  width: 30%;
  max-width: 400px;
  padding: 1.5em 0;
  right: 6em;
  background: radial-gradient(
    76.91% 98.11% at 66.15% 86.6%,
    #e6faff 0%,
    #fff1f1 55.21%,
    #fefdf8 100%
  );
  //background: red;
  border: 2px solid #35aeef;
  box-sizing: border-box;
  border-radius: 8px;
  p {
    color: #6e7191;
    text-align: center;
    //font-weight: lighter;
  }
  span {
    cursor: pointer;
    color: #35aeef;
    text-align: center;
    //background: pink;
    width: 100%;
    @include flexRegular(row, center);
    margin: 0.8em 0 0 0;
    font-size: 0.9em;
  }
}

.modalOverlay {
  width: 100%;
  position: fixed;
  z-index: 5;
  width: 100%;
  height: 100vh;
  transition: 0.6s ease-in-out;
  background: rgba(23, 18, 20, 0.45);
  backdrop-filter: blur(16px);
}
.modal {
  position: fixed;
  top: 15vh;
  left: 25vw;
  right: 25vw;
  width: 50vw;
  background: radial-gradient(
    76.91% 98.11% at 66.15% 86.6%,
    #e6faff 0%,
    #fff1f1 55.21%,
    #fefdf8 100%
  );
  border-radius: 8px;
  z-index: 5;
  @include flexRegular(column, center);
  align-items: center;
  transition: 0.6s ease-in;
}
.modal2 {
  position: fixed;
  top: 7vh;
  left: 27.5vw;
  right: 27.5vw;
  width: 55vw;
  background: none;
  border-radius: 8px;
  z-index: 5;
  @include flexRegular(column, center);
  align-items: center;
  transition: 0.6s ease-in;
  .modalContent {
    width: 100%;
    img {
      width: 100%;
      height: 100%;
      max-height: 400px;
      object-fit: cover;
      border-radius: 15px;
    }
  }
}
.modalContent {
  //background: cornsilk;
  width: 90%;
  margin: 1em 0 3em 0;
  @include flexRegular(column, center);
}
.modalTop {
  //background: lavender;
  width: 100%;
  @include flexRegular(row, flex-start);
  img {
    cursor: pointer;
  }
}
.modalBody {
  margin: 3em 0;
  @include flexRegular(column, center);
  //background: lavender;
  p {
    margin: 1em 0;
    text-align: center;
    color: #6e7191;
    width: 85%;
  }
}
.modalBtn {
  button {
    @include buttonRegular($blueMain, 20vw, 45px);
    max-width: 400px;
    color: #fff;
  }
}

@media (max-width: 750px) {
  .modal,
  .modal2 {
    width: 85vw;
    left: 7.5vw;
    right: 7.5vw;
  }
  .modalBtn {
    button {
      @include buttonRegular($blueMain, 60vw, 45px);
      max-width: 700px;
      color: #fff;
    }
  }
  .notificationBox {
    width: 70%;
    max-width: none;
    padding: 1.5em 0;
    right: 1em;
    background: radial-gradient(
      76.91% 98.11% at 66.15% 86.6%,
      #e6faff 0%,
      #fff1f1 55.21%,
      #fefdf8 100%
    );
    //background: red;
    border: 2px solid #35aeef;
    box-sizing: border-box;
    border-radius: 8px;
    p {
      color: #6e7191;
      text-align: center;
      //font-weight: lighter;
    }
  }
}
@media (max-width: 420px) {
  .modal,
  .modal2 {
    width: 90vw;
    left: 5vw;
    right: 5vw;
  }
}
